import React from 'react'
import {NavLink,withRouter} from 'react-router-dom'
import './nav.css'
class Nav extends React.Component{
    render(){
        return(
            <nav className="home_nav">
                <NavLink to="/home" activeClassName="home_color"></NavLink>
                <NavLink to="/home1" activeClassName="home_color"></NavLink>
                <NavLink to="/home2" activeClassName="home_color"></NavLink>
                <NavLink to="/home3" activeClassName="home_color"></NavLink>
                <NavLink to="/home4" activeClassName="home_color"></NavLink>
                <NavLink to="/home5" activeClassName="home_color"></NavLink>
                <NavLink to="/home6" activeClassName="home_color"></NavLink>
                <NavLink to="/home7" activeClassName="home_color"></NavLink>
                <NavLink to="/home8" activeClassName="home_color"></NavLink>
            </nav>
        )
    }
}
export default withRouter(Nav)