import React from 'react'
import Header from '../layouts/Header'
import Aside from '../component/aside'
import PsyContent from '../component/PsyContent'
import '../assets/css/psychological.css'
import PsyTest from '../component/psyTest/psyTest'

export default class Psychological extends React.Component{
    state={
        data:[]
    }
    async componentDidMount(){
        let res=await axios({url:'/api/goods/home',params:{_page:1,_limit:6}})
        this.setState({data:res.data.data})
    }
    render(){
        let {data}=this.state
        return(
            <div className="psychological">
                <Header topic=""/>
                <Aside>FM</Aside>
                <div className="psyMainContent">
                    {
                        data.map(item=>(
                            <PsyContent 
                                key={item._id}
                                psyData={item}
                            />
                        ))
                    }
                   
                    <div className="psyMore"></div>
                </div>
                <div className="psyBox">
                    <Aside asideStyle={{background:"#a54bbc"}}></Aside>
                    <div className="psyTest">
                        {
                            data.map(item=>(
                                <PsyTest
                                    key={item._id}
                                    psyTestData={item}
                                    to={{pathname:"/details",apiname:"home"}}
                                />
                            ))
                        }
                        {/* <PsyTest
                            psyTestData={{
                                "id":"1",
                                "testImg":"/images/psyTest1.png",
                                "testTitle":"",
                                "testSubTitle":""
                            }}
                        />
                        <PsyTest
                            psyTestData={{
                                "id":"1",
                                "testImg":"/images/psyTest1.png",
                                "testTitle":"",
                                "testSubTitle":""
                            }}
                        /> */}
                    </div>
                </div>
            </div>
           
        )
    }
}